.User {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin:10px;
  padding: 10px;
  background-color:#d5d5d5;
  border-radius: 10px;
  width: 300px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.Image{
  width: 60px;
  height: 60px;
  margin-right:5px;
  border-radius:10px;
  -webkit-border-radius:10px;
  -moz-border-radius:10px;
  -ms-border-radius:10px;
  -o-border-radius:10px;
}

.Name{
  font-size: 1rem;
  color: black;
  margin-left: 20px;
}